import s from "./UserInfo.module.css"
import React, {useEffect, useState} from "react";
import {connect} from "react-redux";
import {getUserProfileStatus, setUserProfile, setUserProfileStatus} from "../../../../../redux/ProfileReducer";
import {compose} from "redux";
import {getUserStatus} from "../../../../../Selectors/Selectors";


let ProfileStatus = (props) => {
    let [editMode, setEditMode] = useState(false)
    let [status, setStatus] = useState(props.mainStatus)

    let activateEditMode = () => {
        setEditMode(true)
    }
    useEffect(() => {
        setStatus(props.mainStatus)
    }, [props.mainStatus])
    let deactivateEditMode = () => {
        setEditMode(false)
        props.setUserProfileStatus(status)
    }
    let onChangeStatus = (e) => {
        setStatus(e.currentTarget.value)
    }
    return (
        <div className={s.statusBox}>
            {!editMode &&
            <div>
                <span onClick={activateEditMode}>{status ? status : "-----"}</span>
            </div>
            }
            {editMode &&
            <div>
                <input value={status} autoFocus={true}
                       onBlur={deactivateEditMode}
                       onChange={onChangeStatus}
                       type="text"/>
            </div>
            }
        </div>
    )

}
let ProfileStatusContainer = (props)=> {
    return (
        <ProfileStatus {...props}/>
    )
}


const mapStateToProps = (state) => {
    return {
        mainUserId: state.auth.mainUserId,
        mainStatus: getUserStatus(state)
    }
}


export default compose(
    connect(mapStateToProps, {
        getUserProfileStatus,
        setUserProfileStatus
    })
)(ProfileStatusContainer);